
.pull-box {
  height: calc(100vh - 100px);
  border: 0px solid green;
  overflow-y: scroll;
}

.plain {
  color: #f64000;
  border: 1px solid #ff2a00;
}

.color {
  background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
  color: #ffffff;
}

.color1 {
  background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
  color: #ffffff;
}

.color0 {
  display: none;
  border: 0;
}

.title {
  font-size: 17px;
  font-weight: normal;
  color: #000000;
  line-height: 17px;
}

.main {
  font-family: PingFang SC-中等, PingFang SC;
}

::v-deep .van-nav-bar__content .van-icon {
  color: black;
}

::v-deep .van-popup .van-icon {
  color: black;
  font-weight: bold;
}

.fsize14 {
  color: #333333;
  font-size: 14px;
  line-height: 16px;
}

.fsize12 {
  font-size: 12px;
  color: #666666;
  line-height: 12px;
}

.fsize13 {
  font-size: 13px;
  line-height: 13px;
  height: 30px;
  border-radius: 1.4rem;
}

.pop-title {
  padding-top: 23px;
  font-size: 20px;
  color: #f92c1a;
  line-height: 20px;
}

.pop-content {
  padding-top: 26px;
  padding-bottom: 34px;
  color: #333333;
  line-height: 30px;
  margin-left: 30px;
  margin-right: 30px;
  text-align: left;
}

.label-man {
  position: absolute;
  left: 30px;
  bottom: 7px;
  border: 0px solid red;
  width: 13px;
  height: 14px;
  overflow: hidden;
}

.menu-box {
  background: #f8f8f8;
  border-radius: 8px;

  font-size: 14px;
}
